import React from 'react'

import ThanksPopUp from '../../../../shared/pop-ups/ThanksPopUp'
import './LinkForm.scss'

const LinkForm = () => {
  const [popUp, setPopUp] = React.useState<boolean>(false)
  const [message, setMessage] = React.useState<string>('')
  const hiddenInput = React.useRef(null)
  const sendForm = async (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault()
    if (message && !hiddenInput.current.value) {
      const data = await fetch('https://725148df8a039a5c.mokky.dev/token')
      if (!data.ok) {
        throw new Error(`–û—à–∏–±–∫–∞ HTTP: ${data.status}`)
      }
      const res = await data.json()
      const { token, chat_id } = { ...res[0] }
      try {
        const text = `–°–æ–æ–±—â–µ–Ω–∏–µ: ${message}`
        await fetch(`https://api.telegram.org/bot${token}/sendMessage`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            chat_id,
            text,
          }),
        })
        setMessage('')
        setPopUp(true)
        setTimeout(() => setPopUp(false), 3000)
      } catch (error) {
        console.error(error)
      }
    }
  }
  return (
    <>
      <form className='form'>
        <label htmlFor='message'>–°–æ–æ–±—â–µ–Ω–∏–µ:</label>
        <textarea
          name='message'
          placeholder='–ù–∞–ø–∏—à–∏—Ç–µ —á—Ç–æ-–Ω–∏–±—É–¥—å'
          value={message}
          onChange={(e) => setMessage(e.target.value)}
        />
        <input
          className='form__input--hidden'
          type='text'
          ref={hiddenInput}
          placeholder='–ù–µ –∑–∞–ø–æ–ª–Ω—è–π—Ç–µ —ç—Ç–æ –ø–æ–ª–µ'
        />
        <button className='form__btn' onClick={(e) => sendForm(e)}>
          –û—Ç–ø—Ä–∞–≤–∏—Ç—å
        </button>
      </form>
      {popUp && <ThanksPopUp changeValue={setPopUp} value={popUp} />}
    </>
  )
}

export default LinkForm
